.tema {
	position: relative;
	width: 100%;
	background-image: url("../images/fundo-raposa.jpg");
	background-repeat: repeat;
}

.bgtema::before {
	content: "";
	position: absolute;
	top: 0;
	right: 0px;
	bottom: 0px;
	left: 0px;

	background-color: rgba(255, 255, 255, 0.6);
}
.texto {
	position: relative;
	color: rgb(0, 0, 0);
	font-weight: 600;
	font-size: 25px;
	padding: 10% 10%;
}

.texto h2 {
	position: relative;
	color: rgb(0, 0, 0);
	font-weight: 600;
	font-size: 50px;
	padding: 3% 10%;
}
