.Card {
	border: 3px solid black;
	border-radius: 10px;
	background-color: white;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	flex: 1;
	margin: 10px;
	min-width: 200px;
}

.Image {
	margin: auto 25px;
	background-color: black;
	color: white;
	align-items: center;
	justify-content: space-around;
	display: flex;
	width: 150px;
	height: 150px;
}
.foto {
	height: 150px;
	width: 150px;
}

.Descricao {
	margin: 10px;
	color: gray;
	display: flex;
	flex-direction: column;
	flex: 1;
}

.Valor {
	font-weight: 300;
}
